body {
  background: #f4f4f4;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 900px;
  margin: auto;
  padding: 20px;
}

.card {
  background: #fff;
  padding: 25px;
  margin-bottom: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
}

.logo {
  height: 55px;
}

label {
  display: block;
  margin-bottom: 12px;
}

input,
select {
  width: 100%;
  padding: 6px;
  margin-top: 4px;
}

table {
  width: 100%;
  margin-top: 10px;
  border-collapse: collapse;
}

td {
  padding: 6px 0;
}

.total td {
  border-top: 2px solid #000;
  font-weight: bold;
}

ul {
  margin-left: 20px;
}

ul li {
  margin-bottom: 4px;
}

.note {
  font-size: 12px;
  margin-top: 10px;
}

.footer {
  margin-top: 30px;
  font-size: 12px;
  border-top: 1px solid #ddd;
  padding-top: 10px;
}

button {
  padding: 10px 20px;
  margin-right: 10px;
  font-size: 15px;
  cursor: pointer;
  background: #1f2937;
  color: #fff;
  border: none;
  border-radius: 4px;
}

button:hover {
  background: #111827;
}

/* OTSIKOT */
h1 {
  font-size: 26px;
}

h2 {
  font-size: 21px;
  font-weight: bold;
  letter-spacing: 1px;
  border-bottom: none;
}

h3 {
  font-size: 18px;
  margin-top: 25px;
}

/* PRINT */
@media print {
  body {
    background: #fff;
  }

  button {
    display: none;
  }

  .card {
    box-shadow: none;
    border: none;
  }
}

